.addTodos {
  display: flex;
  justify-content: center;

  &>.todo-input {
    min-width: 15rem;
    width: 40vw;

    max-height: 2.5rem;
    background-color: #e1ebfd;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;

    align-self: center;

    &:focus {
      outline: none;
      border: 2px solid rbg(67, 58, 168);
    }
  }

  &>.add-btn {
    margin-left: 1rem;
      background-color: #271c6c;
      color: #e1ebfd;
      border-radius: 50%;
      border: 2px solid #e1ebfd;
      font-size: 1.5rem;
      width: 3.2rem;
      height: 3.2rem;
      cursor: pointer;
      box-shadow: 2px 4px 10px #271c6c;
    
      display: flex;
      justify-content: center;
      align-items: center;

      &:focus {
        outline: none;
      }
  }
}